import Star from '../../components/common/starComp/StarZoom'
import noData from '../../components/hasNoData/noData'
export default {
  name: 'Id',
  components: {
    Star,
    noData
  },
  async asyncData (vm) {
    /*
    * vm.params带有一个参数：
    * id:类别id
    * vm.query带有两个参数:
    * category_name:类别名称
    * factory_id：厂商id
    * */
    const categoryName = vm.query.category_name
    const categoryId = vm.params.id
    const factoryId = vm.query.factory_id
    // 某类别下的产品
    const selectCateProRes = await vm.$requests.indexSelectCatePro({
      category_id: vm.params.id
    })
    const selectCateProList = (selectCateProRes.status === 200 && selectCateProRes.data.result === 'success') ? selectCateProRes.data.productlist.slice(0, 9) : []
    const data = {
      CATEGORY_ID: categoryId,
      FACTORY_QCID: factoryId
    }
    const listInfoRes = await vm.$requests.publicCompanyDetail(data)
    let companyInfo = {}
    if (listInfoRes.status === 200 && listInfoRes.data.result === 'success') {
      companyInfo.categoryObj = listInfoRes.data.categoryObj
      companyInfo.factoryQcObj = listInfoRes.data.factoryQcObj
      companyInfo.factoryQcScoreObj = listInfoRes.data.factoryQcScoreObj
    }
    // 某厂商某类别的产品列表
    const listRes = await vm.$requests.publicCompany(data)
    let productList = (listRes.status === 200 && listRes.data.result === 'success') ? listRes.data.productList : []
    return {
      selectCateProList,
      productList,
      categoryName,
      categoryId,
      factoryId,
      companyInfo,
      factory_short_name: companyInfo.factoryQcScoreObj.factory_short_name || companyInfo.factoryQcScoreObj.qcname || '选型宝'
    }
  },
  methods: {
    navigateTo (item) {
      this.$router.push({
        name: 'productDetail-id',
        params: {
          id: item.product_id
        }
      })
    }
  },
  head () {
    return {
      title: this.factory_short_name + ' ' + this.categoryName + '系统_软件的用户实名口碑_选型宝',
      meta: [
        {
          hid: 'description',
          name: 'description',
          content: '想了解' + this.factory_short_name + ' ' + this.categoryName + '系统、' + this.factory_short_name + ' ' + this.categoryName + '软件的用户口碑怎么样？?就上选型宝, 企业级软件的大众点评，实名制的CIO选型经验交流社区'
        },
        {
          hid: 'keywords',
          name: 'keywords',
          content: this.factory_short_name + ' ' + this.categoryName + '系统_' + this.factory_short_name + ' ' + this.categoryName + '软件'
        }
      ]
    }
  }
}
